import React,{Component} from 'react'
import { Layout, Menu, Icon } from 'antd';
import CHeader from '../components/CHeader'
import './BasicLayout.css'
const {  Sider, Content } = Layout;
const SubMenu = Menu.SubMenu;



export default class BasicLayout extends Component {
  constructor(props){
    super(props)
    this.state = {
        collapsed: true,
        layoutmarginLeft:0
    };
    this.delta = this.delta.bind(this);
}
delta(){
    this.setState({
        collapsed: !this.state.collapsed,
    });
}
onCollapse = (collapsed) => {
  console.log(collapsed);
  this.setState({ 
      collapsed ,
      layoutmarginLeft:(collapsed)===true?0:200
    });
}
create = () => {
    this.props.add("Mg Mg")
}

handleOnClick() {
  console.log('intial state')
}
  render() {
    return (
      <Layout>
          <CHeader />

        <Layout style={{background: "white", marginTop: 64 }}>
        <Sider
          breakpoint="lg"
          collapsedWidth="0"
          // style={{ background: "white" }}
          style={{background: "white" , height: '100vh', position: 'fixed' }}
          // trigger={null}
          collapsible
          collapsed={this.state.collapsed}
          onCollapse={this.onCollapse}
        >
          <Menu defaultSelectedKeys={['1']} 
                mode="inline"
                style={{height:'100%'}}>
            <Menu.Item key="1">
              <Icon type="pie-chart" />
              <span>Option 1</span>
            </Menu.Item>
            <Menu.Item key="2">
              <Icon type="desktop" />
              <span>Option 2</span>
            </Menu.Item>
            <SubMenu
              key="sub1"
              title={<span><Icon type="user" /><span>User</span></span>}
            >
              <Menu.Item key="3">Tom</Menu.Item>
              <Menu.Item key="4">Bill</Menu.Item>
              <Menu.Item key="5">Alex</Menu.Item>
            </SubMenu>
            <SubMenu
              key="sub2"
              title={<span><Icon type="team" /><span>Team</span></span>}
            >
              <Menu.Item key="6">Team 1</Menu.Item>
              <Menu.Item key="8">Team 2</Menu.Item>
            </SubMenu>
            <Menu.Item key="9">
              <Icon type="file" />
              <span>File</span>
            </Menu.Item>
          </Menu>
        </Sider>
        <LayoutContent marginLeft={this.state.layoutmarginLeft} />

        </Layout>
      </Layout>
    );
  }
}

const LayoutContent = ({marginLeft}) => {
    const style = {
        marginLeft: marginLeft
    }

    return(
        <Layout style={style}>
               <Content id="content" style={{overflow: 'initial' ,display:'block', margin: '24px 16px', padding: 24, background: '#fff', minHeight: 280 }}>
        <br/>
        <p>sdfdsf</p>
        <br/>
        <p>sdfdsf</p>
        <br/>
        <p>sdfdsf</p>
        <br/>
        <p>sdfdsf</p>
        <br/>
        <p>sdfdsf</p>
        <br/>
        <p>sdfdsf</p>
        <br/>
        <p>sdfdsf</p>
        <br/>
        <p>sdfdsf</p>
        <br/>
        <p>sdfdsf</p>
        <br/>
        <p>sdfdsf</p>
        <br/>
        <p>sdfdsf</p>
        <br/>
        <p>sdfdsf</p>
        <br/>
        <p>sdfdsf</p>
        <br/>
        <p>sdfdsf</p>
        <br/>
        <p>sdfdsf</p>
        <br/>
        <p>sdfdsf</p>
        <br/>
        <p>sdfdsf</p>
        <br/>
        <p>sdfdsf</p>


      </Content>
        </Layout>
 
    )
}


