import React,{Component} from 'react';
import {Input, Layout, Menu, Icon } from 'antd';
const { Header, Content, Footer, Sider } = Layout;


export default class SiderDemo extends React.Component{
    state = { 
        marginLeft: 0 
    };
    render(){


        return(
            <Layout>
                <Sider 
                breakpoint="lg"
                onCollapse={(collapsed, type) => {
                    this.setState({
                        marginLeft:(collapsed)===true? 0:200
                    })
                    // this.setState(previousState => {
                    //     return {trigger: !previousState.hover};
                    //   });
                      console.log(collapsed, type); 
                    }}
                collapsedWidth="0"
                style={{ 
                    // overflow: 'auto',
                    //  height: '100vh',
                    position: 'fixed',
                    left: 0
                    }}
                    >
                <div className="logo" />
                <Menu theme="dark" mode="inline" defaultSelectedKeys={['4']}>
                    <Menu.Item key="1">
                    <Icon type="user" />
                    <span className="nav-text">nav 1</span>
                    </Menu.Item>
                    <Menu.Item key="2">
                    <Icon type="video-camera" />
                    <span className="nav-text">nav 2</span>
                    </Menu.Item>
                    <Menu.Item key="3">
                    <Icon type="upload" />
                    <span className="nav-text">nav 3</span>
                    </Menu.Item>
                    <Menu.Item key="4">
                    <Icon type="bar-chart" />
                    <span className="nav-text">nav 4</span>
                    </Menu.Item>
                    <Menu.Item key="5">
                    <Icon type="cloud-o" />
                    <span className="nav-text">nav 5</span>
                    </Menu.Item>
                    <Menu.Item key="6">
                    <Icon type="appstore-o" />
                    <span className="nav-text">nav 6</span>
                    </Menu.Item>
                    <Menu.Item key="7">
                    <Icon type="team" />
                    <span className="nav-text">nav 7</span>
                    </Menu.Item>
                    <Menu.Item key="8">
                    <Icon type="shop" />
                    <span className="nav-text">nav 8</span>
                    </Menu.Item>
                </Menu>
                </Sider>
               <MyLayout layoutStyle={this.state.marginLeft}/>
            </Layout>
        
        )
    }
}

export const MyLayout = ({layoutStyle}) => {
    const layoutStyle1 = {
        marginLeft: layoutStyle
    }
    return(
        <Layout style={layoutStyle1}>
        {/* <Header style={{ background: '#fff', padding: 0 }} /> */}
        <Content style={{ margin: '24px 16px 0', overflow: 'initial' }}>
            <div style={{ padding: 24, background: '#fff' }}>
            <Input size="large"/>
            ...
            <br />
            Really
            <br />...<br />...<br />...<br />
            long
            <br />...<br />...<br />...<br />...<br />...<br />...
            <br />...<br />...<br />...<br />...<br />...<br />...
            <br />...<br />...<br />...<br />...<br />...<br />...
            <br />...<br />...<br />...<br />...<br />...<br />...
            <br />...<br />...<br />...<br />...<br />...<br />...
            <br />...<br />...<br />...<br />...<br />...<br />...
            <br />...<br />...<br />...<br />...<br />...<br />
            content
            </div>
        </Content>
        <Footer style={{ textAlign: 'center' }}>
            Ant Design ©2016 Created by Ant UED
        </Footer>
        </Layout>
    )

}



